export default {
	data() {
		return {
			keywords:'',
			nav_type:2001,
			sta:2,
			
			meeting_list:[],
			pagemax:'',//总的数量
			pagenow:0,//当前的页数
			shownumber:6,//每页的分页条数
			
			contentText: {
				contentdown: "查看更多",
				contentrefresh: "加载中",
				contentnomore: "没有更多"
			},
			status:'loading',
			shouLoading:true,
			haveData:false,
		}
	},
	onLoad() {
		var t=this;
		
		// 获取列表
		t.getMeetingList();
		
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.shownumber >= this.pagemax) {
			return;
		}
		setTimeout(() => {
			this.setDate();
		}, 100);
	},
	onPullDownRefresh() {
		this.initData();
	},	
	methods: {
		goDetail:function(id){
			uni.navigateTo({
				url: '../index/meeting-detail?id='+id+"&sign=true"
			})
		},
		navChange:function(nav_type){
			var t=this;
			t.nav_type=nav_type;
			t.getMeetingList();
		},
		getMeetingList:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				meeting_type:t.nav_type,
				status:t.sta,
			}
			t.urlRequest.urlRequest(t.func.getMeetingList,data,function(res){
				t.meeting_list=res.data.list;
				t.pagemax=res.data.count;
				uni.hideLoading();
			})
		},
		initData(){
			uni.showLoading();
			const t = this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				meeting_type:t.nav_type,
				status:t.sta,
			}
			t.urlRequest.urlRequest(t.func.getMeetingList,{party_no:t.globalData.userInfo.party_no},function(res){
				t.meeting_list=res.data.list;
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		},
		setDate() {
			uni.showLoading();
			this.shownumber=this.pagenow*6;
			this.pagenow++;
			var t=this;
			
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				meeting_type:t.nav_type,
				status:t.sta,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getMeetingList,data,function(res){
				if(res.data.list){
					setTimeout(() => {
						t.haveData =true
					}, 100);
					
					t.meeting_list = t.meeting_list.concat(res.data.list);
					t.pagemax=res.data.count
				}
				uni.hideLoading();
			})
		},
	}
}
